.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#loader{
  width: 100%;
  grid-column: 1 / -1;
}
#loader .clones2 {
  width:28px;
  height:28px;
  border-radius: 50%;
  background:#F10C49;
  animation: cl2 1.5s infinite;
  margin: 1em auto;
  }
  
@keyframes cl2 {
  0%,
  100%{transform:translate(-35px);box-shadow:  0     0 #F4DD51, 0     0 var(--cyan)}
  40% {transform:translate( 35px);box-shadow: -15px  0 #F4DD51,-30px  0 var(--cyan)}
  50% {transform:translate( 35px);box-shadow:  0     0 #F4DD51, 0     0 var(--cyan)}
  90% {transform:translate(-35px);box-shadow:  15px  0 #F4DD51, 30px  0 var(--cyan)}
  }